/**
 * @module ui-bb-account-statements-filter-ng
 * @description
 * UI account statements filter component
 *
 * @example
 * // In an extension:
 * // file: scripts/index.js
 * import uiBbAccountStatementsFilterKey from 'ui-bb-account-statements-filter-ng';
 *
 * export const dependencyKeys = [
 *   uiBbAccountStatementsFilterKey,
 * ];
 *
 * // file: templates/template.ng.html
 * <ui-bb-account-statements-filter-ng on-filter="$ctrl.filter()">
 * </ui-bb-account-statement-filter-ng>
 */

import angular from 'vendor-bb-angular';

import uiBbCalendarPopupKey from 'ui-bb-calendar-popup-ng';
import uiBbTrackChangesKey from 'ui-bb-track-form-changes-ng';
import uiBbAccountStatementsCategoryFilterKey from 'ui-bb-account-statements-category-filter-ng';

import Component from './component';
import Controller from './controller';

/**
 * @name default
 * @type {string}
 * @description The angular module name
 */
export default angular
  .module('ui-bb-account-statements-filter-ng', [
    uiBbCalendarPopupKey,
    uiBbTrackChangesKey,
    uiBbAccountStatementsCategoryFilterKey,
  ])
  .component('uiBbAccountStatementsFilterNg', Component)
  .controller('uiBbAccountStatementsFilterController', [Controller])
  .name;



