
/**
 * @name uiBbAccountStatementsFilter
 * @type {Object}
 *
 * @property {Function} onFilter Function to be called once the filter is applied
 * @property {Function} onClear Function to be called once the filter is cleared
 * @property {ButtonLabels} buttonLabels Object with translated button labels
 * @property {FieldLabels} fieldLabels Object with translated field labels
 * @property {Object} datepickerConfig Object with config for datePicker
 */
const uiBbAccountStatementsFilterComponent = {
  bindings: {
    onFilter: '&',
    onClear: '&?',
    buttonLabels: '<',
    fieldLabels: '<',
    datepickerConfig: '<',
    categoryConfig: '<',
    showCategories: '<',
  },
  controller: 'uiBbAccountStatementsFilterController',
  template: `
  <div class="card card-flat">
    <form data-role="search"
      name="searchFilterForm"
      data-ng-submit="$ctrl.onApplyFilter(searchFilterForm);$ctrl.toggleFilter()"
      data-ui-bb-track-changes-ng="$ctrl.state.params">
      <div class="card-header">
        <div class="col-12 p-0 text-right">
          <button type="button"
            data-ng-class="{ 'active' : $ctrl.state.isFilterOpen }"
            class="btn btn-secondary btn-filter"
            data-role="filter"
            data-ng-click="$ctrl.toggleFilter()"
            title="{{ $ctrl.buttonLabels.main }}">
            <i class="fa fa-filter" aria-hidden="true"></i>
            {{ $ctrl.buttonLabels.main }}
          </button>
        </div>
      </div>
      <div data-ng-show="$ctrl.state.isFilterOpen">
        <div class="card-body card-body-dark">
          <div class="row">
            <div class="col-lg-6">
              <div class="form-group"
                data-ng-if="$ctrl.categoryConfig 
                  && $ctrl.categoryConfig.categories.length > 0 
                  && $ctrl.showCategories">
                <label data-role="category-label"
                  data-ng-bind="$ctrl.fieldLabels.category">
                </label>
                <ui-bb-account-statements-category-filter-ng
                  data-ng-model="$ctrl.state.params.category"
                  data-on-change="searchFilterForm.$setDirty()"
                  data-multi="$ctrl.categoryConfig.isMultiCategory"
                  data-options="$ctrl.categoryConfig.categories">
                </ui-bb-account-statements-category-filter-ng>
              </div>
              <div class="form-group">
                <label data-role="date-range-label"
                  data-ng-bind="$ctrl.fieldLabels.dateRange">
                </label>
                <ui-bb-calendar-popup-ng class="date-range-start"
                  data-config="$ctrl.state.datePickerConfig"
                  data-date-range="$ctrl.state.params.dateRange"
                  disabled="false"
                  data-role="date-range">
                </ui-bb-calendar-popup-ng>
              </div>
            </div>
          </div>
        </div>
        <div class="card-footer card-footer-darker">
          <div class="row">
            <div class="col-12">
              <div class="d-none d-md-block text-right">
                <button type="button"
                  class="btn btn-link"
                  data-ng-show="searchFilterForm.$dirty"
                  data-ng-click="$ctrl.onClearFilter(searchFilterForm)"
                  title="{{ $ctrl.buttonLabels.clearAll }}"
                  data-role="clear-all"
                  data-ng-bind="$ctrl.buttonLabels.clearAll">
                </button>
                <button type="button"
                  class="btn btn-secondary"
                  data-ng-click="$ctrl.toggleFilter()"
                  title="{{ $ctrl.buttonLabels.closePanel }}"
                  data-role="cancel"
                  data-ng-bind="$ctrl.buttonLabels.closePanel">
                </button>
                <button data-ng-disabled="$ctrl.isFormValid(searchFilterForm)"
                  class="btn btn-primary"
                  title="{{ $ctrl.buttonLabels.apply }}"
                  data-role="apply"
                  data-ng-bind="$ctrl.buttonLabels.apply">
                </button>
              </div>
              <div class="d-block d-md-none">
                <button
                  data-ng-disabled="$ctrl.isFormValid(searchFilterForm)"
                  class="btn btn-primary btn-block"
                  title="{{ $ctrl.buttonLabels.apply }}"
                  data-role="apply"
                  data-ng-bind="$ctrl.buttonLabels.apply">
                </button>
                <button
                  type="button"
                  class="btn btn-secondary btn-block"
                  data-ng-click="$ctrl.toggleFilter()"
                  title="{{ $ctrl.buttonLabels.closePanel }}"
                  data-role="cancel"
                  data-ng-bind="$ctrl.buttonLabels.closePanel">
                </button>
                <button
                  type="button"
                  class="btn btn-link btn-block"
                  data-ng-show="searchFilterForm.$dirty"
                  data-ng-click="$ctrl.onClearFilter(searchFilterForm)"
                  title="{{ $ctrl.buttonLabels.clearAll }}"
                  data-role="clear-all"
                  data-ng-bind="$ctrl.buttonLabels.clearAll">
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
  `,
};

export default uiBbAccountStatementsFilterComponent;

/**
 * Labels type definition
 * @typedef {Object} ButtonLabels
 * @property {String} main Label for main filter button
 * @property {String} apply Label for button that applies filter
 * @property {String} closePanel Label for button that closes filter card
 * @property {String} clearAll Label for button that clears filter
 */

/**
 * Labels type definition
 * @typedef {Object} FieldLabels
 * @property {String} dateRange Datepicker label
 */



